import React from 'react'

const Feedback = () => {
    return (
        <div className="card h-100">
            <div className="card-header">
                <h2>Плохой отзовик VS хороший</h2>
            </div>
            <div className="card-body">
                <p className="lead"><strong>FAQ</strong>. Верить или нет отзовикам? Как мошенники обманывают нас через отзовики?</p>
                <p className="lead">Сайт zhaloba-online создан для того, чтобы люди могли оставлять положительные или отрицательные отзывы, плюсы и минусы, жалобы, комментировать. Но сегодня похожие интернет ресурсы существую (и не долго) лишь для продвижения сайтов мошенников, мошеннических организаций и физ. лиц, ИП, занимающихся мошеннической деятельностью.</p>
                <p className="lead">Внесу ясность по типу мошенников: <strong>А-Б-В</strong></p>
                <p className="lead"><strong>а)</strong> <strong>Сайты мошенники</strong>. Их владельцев вы никогда не найдете, если вас обманули на деньги, и не сможете вернуть ущерб и наказать их, даже органы их не найдут. В 99% случаев поиски оканчиваются ничем (VPN, местонахождение в другой стране и тд, банально ваше заявление в полиции отложат и забудут). Сайты могут быть как однодневки, так и большие каталоги, где товары будут стоить дешевле чем в известных интернет магазинах на 15, 20, а то и 50 процентов. Важно: не переводите деньги на карту физ. лица, яндекс деньги или киви кошелек, если не уверены и проверяйте возраст домена через "whois", если сайт свежий, к нему доверие стремится к нулю.</p>
                <p className="lead"><strong>б)</strong> <strong>Мошеннические организации</strong>. <em>Вариант 1</em>: Регистрируется юридическое лицо на "левого" человека, самое простое "ООО" - Общество с Ограниченной Ответственностью. Левый человек - он же номинальный директор (формальный руководитель компании, не участвующий в процессе управления).&nbsp;<em>Вариант 2</em>: Покупается <a href="https://zhaloba-online.ru/kompaniya-odnodnevka-moshennik">готовая ООО'шка</a> в даркнете вместе с расчетным счетом. Дальше создается сайт под фирму-однодневку, запускается реклама в интернете, показ объявлений через Яндекс/Гугл директ. Цены ставят обычно ниже рыночных процентов на 30, а то и больше. Мошенники также могут зарегистрироваться на сторонних площадках (tiu, metal100 т.д. т.п.), по типу куплю/продам оборудование, станки, сырье, в общем промышленные доски объявлений, где можно быть user-продавец или user-покупатель. User-продавец, он же мошенник, находит доверчивого user-покупателя и через переписку или по телефону сообщает, что есть нужная для вас позиция по очень низкой цене, но мы работаем только по предоплате (50-100%). Контрагент клюет на низкую стоимость и оплачивает счет. С печатью, с подписями, все как положено. А дальше люди, с которыми вы возможно общались по почте или телефону перестают выходить на связь, а деньги с расчетного счета выводятся через подставные фирмы или ИП. На одной ООО'шке мошенники могут зарабатывать миллионы, а честные предприниматели нести огромный убыток. Обратите внимание, если вы осуществляли перевод из одной страны в другую, то скорее всего мошенников не найдут. Важно: не платите на карту физ. лиц, Яндекс.Деньги, Qiwi кошелек и другие электронные средства оплаты, если не уверены в сделке.</p>
                <p className="lead"><strong>в)</strong> <strong>Физ. лица, ИП</strong>. Большинство из них дают объявления на авито, у них также могут быть сайты. В основном это черные риэлторы, <a href="https://zhaloba-online.ru/otzyvy/psevdo-brokery-moshenniki-kak-obmanyvayut.html">псевдоброкеры</a>, колдуны, экстрасенсы, <a href="https://zhaloba-online.ru/otzyvy/yuristy-moshenniki-kak-rabotayut-i-kak-ne-popastsya.html">мошенники-юристы</a>, мошенники по ремонту бытовой и эл. техники, мастера ремонтных и отделочных работ, частные детективы, помощь в оформлении кредита, загранпаспорта, визы и других документов. Совет, если не уверены - работайте с ними <strong>только</strong> по договору, в котором будут указаны паспортные данные обеих сторон, никаких удаленных переводов и предоплат на карты, яндекс деньги и киви кошельки.</p>
                <p className="lead"><strong>Плохие отзовики.</strong> Созданы специально мошенниками для публикации на них правильной, нужной им информации (в положительном ключе), накручивание рейтинга и повышение репутации сайта-мошенника, мошеннических юр. и физ. лиц. Вытеснение из первых строк поисковика достоверных, честных отзывов, с целью скрыть правду от пользователей, которые ищут, хотят приобрести тот или иной товар, оплатить определенную услугу.</p>
                <p className="lead"><strong>Хорошие отзовики</strong>. Тут все просто. Независимые сайты, на которых публикуется различная информация с некоторыми отличиями, касающимися публикации, модерации и оформления отзывов в целом. Но даже на хороших отзовиках мошенники продвигают свои товары и услуги. Касаемо организаций у них есть ИНН, КПП, ОГРН, расчетный счет. Используя эти данные вы можете поискать информацию в открытых источниках. Когда была зарегистрирована компания, суды, иски, уставной капитал, учредитель/руководитель/генеральный директор и другие данные, которые для вас могут оказаться полезными.</p>
            </div>
        </div>
    )
}

export default Feedback;
